*{
    font-family: Helvetica, Arial, sans-serif;
}
body{
    padding: 20px;
}
input{
    font-size: 1.2rem;
    width: 80%;
    color: gray;
}
.card{
    align-content: center;
    border: 1px solid gray;
    border-radius: 6px;
    overflow: hidden;
    margin-bottom: 10px;
    margin-right: 10px;
}
.card-grid{
    display: flex;
    flex-direction: row;
    flex-wrap: wrap;
}
.card p {
    padding: 5px;
    text-align: center;
}
.card img{
    max-height: 170px;
}